					</editor> -->
					<editor id="editor" class="ql-container" :placeholder="placeholder" @statuschange="onStatusChange"
						:show-img-resize='true' @ready="onEditorReady" @input='getCtx'>
					</editor>
				</view>
				
				<view class='toolbar' @tap="format" style="height: 120rpx;overflow-y: auto;margin-top: 100rpx;">
					<view :class="formats.bold ? 'ql-active' : ''" class="iconfont icon-zitijiacu" data-name="bold">
					</view>
