					<view :class="formats.marginTop ? 'ql-active' : ''" class="iconfont icon-722bianjiqi_duanqianju"
						data-name="marginTop" data-value="20rpx"></view>
					<view :class="formats.previewarginBottom ? 'ql-active' : ''"
