<script>
	export default {
		props: {
			value: {
				type: String,
				default: ''
			}
		},
		data() {
			return {
				readOnly: false,
				placeholder: '开始输入...',
				richText: '',
				formats: {},
				serverUrl: this.$http.serverUrl, // 图片上传 - 接口
			}
		},
 
		methods: {
			readOnlyChange() {
				this.readOnly = !this.readOnly
			},
			onEditorReady() {
				// 富文本节点渲染完成
				const query = uni.createSelectorQuery().in(this)
				query.select('#editor').context((res) => {
					this.editorCtx = res.context
					if (this.value) {
						this.editorCtx.setContents({
							html: this.value
						})
					}
				}).exec(this)
			},
			// 失去焦点时，获取富文本的内容
			getCtx(e) {
				this.richText = e.detail.html
				this.$emit('input', e.detail.html)
			},
			undo() {
				this.editorCtx.undo()
			},
			redo() {
				this.editorCtx.redo()
			},
			format(e) {
				let {
					name,
					value
				} = e.target.dataset
				if (!name) return
				// console.log('format', name, value)
				this.editorCtx.format(name, value)
 
			},
			onStatusChange(e) {
				const formats = e.detail
				this.formats = formats
			},
			insertDivider() {
				this.editorCtx.insertDivider({
					success: function() {
						console.log('insert divider success')
					}
				})
			},
			clear() {
				this.editorCtx.clear({
					success: function(res) {
						console.log("clear success")
					}
				})
			},
			removeFormat() {
				this.editorCtx.removeFormat()
			},
			insertDate() {
				const date = new Date()
				const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
				this.editorCtx.insertText({
					text: formatDate
				})
			},
			insertImage() {
				var that = this;
				uni.chooseMedia({
				  count: 1,
				  mediaType: ['image'],
				  sourceType: ['album', 'camera'],
				  camera: 'back',
				  success(res) {
					  var tempImg = res.tempFiles[0].tempFilePath;
					  uni.uploadFile({
					  	url: 'https://tdpm.shningmi.com/applet/uploads',
					  	filePath: tempImg,
					  	name: 'file',
					  	success: (uploadFileRes) => {
							var data = JSON.parse(uploadFileRes.data);
							var img = data.data.url;
							// uploadFileRes.data.match('https://(.*)')
							that.editorCtx.insertImage({
								width: '100%', //设置宽度为100%防止宽度溢出手机屏幕
								height: 'auto',
								src: img,
								alt: '图像',
								success: function(info) {
									console.log(info,'insert image success')
								}
							})
					  	}
					  });
				  }
				})
				
				
				
				
// 				uni.chooseImage({
// 					count: 1,
