// 										}
// 									})
// 								}else{
// 									this.$http.toast('上传失败')
// 								}
// 							},
// 							fail: (err) => {
// 								console.log(err)
// 							}
// 						})
// 					}
// 				})
			},
 
		},
		onLoad() {
			uni.loadFontFace({
				family: 'Pacifico',
				// source: 'url("https://sungd.github.io/Pacifico.ttf")'
			})
		},
	}
